<template>
  <div id="index">
    <h1>{{msg}}</h1>
  </div>
</template>

<script>
export default {
  data () {
    return {
      msg: '跳转登录页面...'
    }
  },
  
  created() {
    this.$router.push({ path: '/notebooks' })
  }

}
</script>

<style scoped>
h1 {
  color: red;
}
</style>


